@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Rajdhani&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:#0f111a}.App{min-height:100vh;display:flex;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);font-family:"Rajdhani", sans-serif;font-weight:bold;color:#fff;flex-direction:column}.App .grid{background-color:rgba(31,34,51,0.6);border-radius:10px;border:0.4px groove rgba(254,254,254,0.7);display:grid;max-width:259px;max-height:405px;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-template-areas:"dis dis dis dis" "dis dis dis dis" "clear backspace divide multiply" "seven eight nine subtract" "four five six add" "one two three equals" "zero zero decimal equals"}.App .grid .dis{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);grid-area:dis;text-align:right;padding:10px 20px;border-top-left-radius:10px;border-top-right-radius:10px}.App .grid .dis .expression{text-align:right;font-size:0.7em;border:none;background-color:inherit;color:inherit}.App .grid .dis .total{font-size:1.8em;overflow:auto;-ms-scroll-snap-type:none;scroll-snap-type:none}.App .grid .padButton{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);display:flex;align-items:center;justify-content:center;border:0.4px groove rgba(254,254,254,0.7);padding:10px 20px;cursor:pointer;font-size:0.8em}.App .grid #zero{grid-area:zero;border-bottom-left-radius:10px}.App .grid #equals{grid-area:equals;border-bottom-right-radius:10px}.App #da3ker{color:white;font-size:0.55em;letter-spacing:2px;font-family:"Rampart One", cursive;margin-top:0.5em}::-webkit-scrollbar{height:7px}::-webkit-scrollbar-track{background:rgba(254,254,254,0.3);border-radius:10px}::-webkit-scrollbar-thumb{background:rgba(250,253,252,0.4);border-radius:10px}::-webkit-scrollbar-thumb:hover{background:rgba(245,250,249,0.9)}.padButton:hover{color:#75d1e2;background-color:rgba(245,250,249,0.1)}

